/**
 * 判断设备类型 EQUIPMENT_TYPE
 * @method NAVIGATOR
 */
export const NAVIGATOR = () => {
  var ua = window.navigator.userAgent.toLowerCase()
  if (ua.match(/MicroMessenger/i) === 'micromessenger') {
    return 'Wechat'
  } else {
    var u = window.navigator.userAgent
    if (u.indexOf('Android') > -1 || u.indexOf('Linux') > -1) {
      return 'Android'
    } else if (u.indexOf('iPhone') > -1 || u.indexOf('iPad') > -1) {
      return 'IOS'
    } else {
      return 'NULL'
    }
  }
}

/**
 * 位数格式化
 * @method digitFormat
 * @param  {string}    val      初始字符串, 必选
 */
export const digitFormat = (val) => {
  if (val < 10) {
    val = '0' + val
  }
  return val
}

/**
 * 数字转字符串时间
 * @method numToTimeFormat
 * @param  {number}      val      数字, 必选
 */
export const numToTimeFormat = (val) => {
  let hour = digitFormat(parseInt(val / 60))
  let minute = digitFormat(val - parseInt(val / 60) * 60)
  return `${hour}:${minute}`
}



