let zhErrorCode = []
let enErrorCode = []
Object.keys(error).map(function (key) {
  zhErrorCode[error[key].include] = error[key].zh
  enErrorCode[error[key].include] = error[key].en
})
// console.log(zhErrorCode)
// console.log(enErrorCode)

export const errorHandle = (code) => {
  if (errorCode[code]) {
    console.log('error:')
    if (i18n.locale === 'zh') {
      store.dispatch('alertErrorMsg', zhErrorCode[code])
    } else {
      store.dispatch('alertErrorMsg', enErrorCode[code])
    }
  }
}



