<template>
  <div class="errorCode">
    <toast :toastTip.sync="$store.state.errorLog.errorMsgToast"
           :describe="errorMsg"
           position="bottom"></toast>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
import toast from '../../components/Toast/index'
export default {
  name: 'errorCode',
  components: {
    toast
  },
  computed: {
    ...mapGetters(['errorMsg'])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.errorCode{}
</style>



