import Mock from 'mockjs'
import loginAPI from './login'
import scanningAPI from './scanning'

// 登录相关
Mock.mock('/login', 'get', loginAPI.loginByUsername)

// 扫描流程
Mock.mock('/scanning/1', 'get', scanningAPI.validateFun)
Mock.mock('/scanning/2', 'get', scanningAPI.validateTrue)
Mock.mock('/scanning/3', 'get', scanningAPI.validateFalse)
Mock.mock('/scanning/index', 'get', scanningAPI.scanningFun)

export default Mock



