const map = {
  state: {
    position: ''
  },
  mutations: {
    SET_CENTER: (state, pos) => {
      state.position = pos
    }
  },
  actions: {
    setPosition ({ commit }, pos) {
      commit('SET_CENTER', pos)
    }
  }
}

export default map



