import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/home',
    name: 'home',
    component: () => import('@/views/home/index')
  },
  {
    path: '/login', // 登录
    component: () => import('@/views/login/login')
  },
  {
    path: '/personal', // 个人中心
    component: () => import('@/views/personal/personal')
  },
  {
    path: '/rename',
    component: () => import('@/views/personal/rename'),
    name: 'rename'
  },
  {
    path: '/wallet', // 我的钱包
    component: () => import('@/views/wallet/wallet')
  },
  {
    path: '/recharge',
    component: () => import('@/views/wallet/recharge')
  },
  {
    path: '/deposit',
    component: () => import('@/views/wallet/deposit')
  },
  {
    path: '/transaction',
    component: () => import('@/views/wallet/transaction')
  },
  {
    path: '/withdraw',
    component: () => import('@/views/wallet/withdraw')
  },
  {
    path: '/order', // 我的订单
    component: () => import('@/views/order/order')
  },
  {
    path: '/about', // 关于我们
    component: () => import('@/views/about/about')
  },
  {
    path: '/feedback', // 意见反馈
    component: () => import('@/views/feedback/feedback')
  },
  {
    path: '/feedbackSuccess',
    component: () => import('@/views/feedback/feedbackSuccess')
  },
  {
    path: '/feedbackFailure',
    component: () => import('@/views/feedback/feedbackFailure')
  },
  {
    path: '/language', // 多语言设置
    component: () => import('@/views/language/index')
  },
  {
    path: '/store', // 店铺分类
    component: () => import('@/views/store/store')
  },
  {
    path: '/details', // 店铺详情
    component: () => import('@/views/store/details')
  },
  {
    path: '/charger', // 扫码
    component: () => import('@/views/scanning/charger')
  },
  {
    path: '/charging',
    component: () => import('@/views/scanning/lease/charging')
  },
  {
    path: '/takingCharger',
    component: () => import('@/views/scanning/lease/takingCharger'),
    name: 'takingCharger'
  },
  {
    path: '/takeSuccessfully',
    component: () => import('@/views/scanning/lease/takeSuccessfully')
  },
  {
    path: '/takeFailure',
    component: () => import('@/views/scanning/lease/takeFailure')
  },
  {
    path: '/restituteCharger',
    component: () => import('@/views/scanning/restitute/restituteCharger')
  },
  {
    path: '/restituteSuccessfully',
    component: () => import('@/views/scanning/restitute/restituteSuccessfully')
  }]
})

// 根据具体的跳转类型更改跳转属性值，执行不同的动画
const nextDirection = (direction) => {
  let el = document.getElementById('app')
  if (el) el.setAttribute('transition-direction', direction)
}

router['_push'] = router['push']

// 重写路由跳转方法，设置跳转类型后跳转
router.forward = router['push'] = (target) => {
  nextDirection('forward')
  setTimeout(() => { router['_push'](target) })
}

// 重写路由返回方法，设置跳转类型后跳转到上一页
router.back = (target) => {
  nextDirection('back')
  if (target) {
    setTimeout(() => { router['_push'](target) })
  }
  history.go(-1)
}

export default router



