<i18n>
{
  "en": {
    "PleaseWait": "Please wait...",
    "retry": "Operation timed out, please retry"
  },
  "zh": {
    "PleaseWait": "请稍后...",
    "retry": "操作超时，请重试"
  }
}
</i18n>

<template>
  <div class="loadingAlert">
    <transition name="delay">
      <div class="mask" v-if="loading"></div>
    </transition>
    <transition name="fade">
      <div class="load" v-if="loading">
        <span>
          <svg height="100%" width="100%" xmlns="http://www.w3.org/2000/svg" version="1.1">
            <circle id="shape1" cx="28" cy="28" r="23" stroke="#8B8B8B" stroke-width="4" fill="transparent" />
            <circle id="shape2" cx="28" cy="28" r="23" stroke="#FFF" stroke-width="4" fill="transparent"/>
          </svg>
        </span>
        <strong>{{$t('PleaseWait')}}</strong>
      </div>
    </transition>

    <toast :toastTip.sync="$store.state.app.loadingToast"
           :describe="$t('retry')"
           position="bottom"></toast>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import toast from '../Toast/index'
export default {
  name: 'loadingAlert',
  components: {
    toast
  },
  computed: {
    ...mapGetters(['loading'])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.loadingAlert{
  .mask{
    .fs;
    background: rgba(0, 0, 0, 0.5);
  }
  @loadHeight: 240px;
  .load{
    .wh(@loadHeight, @loadHeight);
    border-radius: 12px;
    background: rgba(0, 0, 0, 0.55);
    position: absolute;
    left: 50%;
    top: 44%;
    margin: -@loadHeight/2 0 0 -@loadHeight/2;
    @runHeight: 112px;
    span{
      .wh(@runHeight, @runHeight);
      display: block;
      position: absolute;
      left: 50%;
      top: 38%;
      margin: -@runHeight/2 0 0 -@runHeight/2;
      #shape2{
        stroke-dasharray: 39.27 400;
        animation: rotate 1s infinite;
        transform-origin: center center;
      }
    }
    strong{
      .wh-l(100%, 40px);
      display: block;
      font-size: 30px;
      position: absolute;
      left: 0;
      bottom: 13%;
      color: #FFF;
    }
  }
}
.delay-leave-active{
  transition-delay: .2s;
}
.fade-enter-active,.fade-leave-active{
  transition: all .3s ease-out;
}
.fade-enter, .fade-leave-to{
  opacity: 0;
}
</style>



