import axios from 'axios'
import store from './store/index'
import { NAVIGATOR } from './utils/index'
import router from './router'
import { errorHandle } from './components/ErrorCode/errorCode'

let base = {}
base.install = function (Vue) {
  //  HTTP请求
  Vue.prototype.$http = axios

  // 调试模式
  Vue.prototype.ISDEBUG = (process.env.NODE_ENV === 'development')

  /**
   * 数据交互请求接口
   * @method dataInteractionRequest
   * @param  {string}   data        需要发送的数据，必选
   */
  Vue.prototype.dataInteractionRequest = function (data) {
    console.log('data:')
    console.log(data)
    if (Vue.prototype.ISDEBUG) return false
    switch (NAVIGATOR()) {
      case 'IOS':
        let obj = {
          'data': JSON.stringify(data)
        }
        window.webkit.messageHandlers.dataInteractionRequest.postMessage(obj)
        break
      case 'Android':
        window.Data.dataInteractionRequest(JSON.stringify(data))
        break
    }
  }

  /**
   * 数据交互响应接口
   * @method dataInteractionResponse
   */
  Vue.prototype.dataInteractionResponse = function (fn) {
    switch (NAVIGATOR()) {
      case 'IOS':
      case 'Android':
        window.dataInteractionResponse = function (data) {
          let obj = JSON.parse(data)
          console.log('obj:')
          console.log(obj)
          // console.log(router.currentRoute.fullPath)
          if (obj.msgType === '0x5001' && router.currentRoute.fullPath !== '/home') {
            router.back()
          }
          // 系统语言变更返回
          if (obj.msgType === '0x5043') {
            if (obj.result && obj.data) {
              store.dispatch('setLanguage', obj.data.language)
            }
          }
          // 归还充电宝
          if (obj.msgType === '0x5212') {
            if (obj.result) {
              router.push('/restituteSuccessfully')
            } else {
              // 暂定还入失败提示
              errorHandle('0x830201')
            }
          }
          if (obj.data) {
            errorHandle(obj.data.errorCode)
          }
          // 隐藏加载框
          store.dispatch('loadingAlert', false)
          fn(obj)
        }
        break
    }
  }
}
export default base



