const errorLog = {
  state: {
    logs: '',
    errorMsg: '', // 响应错误信息
    errorMsgToast: false
  },
  mutations: {
    ADD_ERROR_LOG: (state, log) => {
      state.logs = log
    },
    ALERT_ERROR_MSG: (state, txt) => {
      state.errorMsg = txt
    }
  },
  actions: {
    addErrorLog ({ commit }, log) {
      commit('ADD_ERROR_LOG', log)
    },
    alertErrorMsg ({commit, state}, txt) {
      commit('ALERT_ERROR_MSG', txt)
      state.errorMsgToast = true
    }
  }
}

export default errorLog



