const getters = {
  errorLog: state => state.errorLog.logs,
  errorMsg: state => state.errorLog.errorMsg,
  position: state => state.amap.position,
  sidebarShow: state => state.app.sidebarShow,
  loading: state => state.app.loading,
  loadingToast: state => state.app.loadingToast,
  isLogin: state => state.user.isLogin,
  userInfo: state => state.user.userInfo,
  chargerId: state => state.user.chargerId
}

export default getters



