<template>
  <div id="app">
    <transition name="fade-effect" mode="in-out">
      <keep-alive :include="['home']">
        <router-view/>
      </keep-alive>
    </transition>

    <error-log></error-log>
    <loading-alert></loading-alert>
    <!-- 返回测试按钮 -->
    <!-- <div class="phone_back" @click="back" v-if="ISDEBUG">←</div> -->
  </div>
</template>

<script>
import errorLog from './components/ErrorCode/index'
import loadingAlert from './components/Loading/index'
export default {
  name: 'App',
  components: {
    errorLog,
    loadingAlert
  },
  mounted () {
    // 写入历史记录
    this.$router.push('/home')
  },
  methods: {
    back () {
      // let str = '{"msgType": "0x5001","data":{}}'
      let str = '{"msgType":"0x5043","result":true,"data":{"language":"zh"}}'
      window.dataInteractionResponse(str)
      // if (this.$i18n.locale === 'en') {
      //   this.$i18n.locale = 'zh'
      // } else {
      //   this.$i18n.locale = 'en'
      // }
    }
  },
  watch: {
    '$route' (to, from, next) {
      this.dataInteractionResponse(data => {
        // console.log(data)
      })
    }
  }
}
</script>

<style lang="less">
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #585858;
    width: 100%;
    height: 100%;
    overflow: hidden;
    .phone_back{
      position: absolute;
      top: 300px;
      z-index: 999;
      .wh-l(100px, 100px);
      font-size: 60px;
      background: @themeColor;
      color: #FFF;
    }
  }

  .fade-effect-enter-active {
    -webkit-transition-duration: 260ms;
    transition-duration: 260ms;
    -webkit-transition-timing-function: cubic-bezier(0.4, 0.6, 0.2, 1);
    transition-timing-function: cubic-bezier(0.4, 0.6, 0.2, 1);
  }
  .fade-effect-leave-active {
    -webkit-transition-duration: 200ms;
    transition-duration: 200ms;
    -webkit-transition-timing-function: cubic-bezier(0.4, 0.6, 0.2, 1);
    transition-timing-function: cubic-bezier(0.4, 0.6, 0.2, 1);
  }

  [transition-direction="forward"] .fade-effect-enter {
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
    // opacity: 1;
    // z-index: 2;
    z-index: 9999;
  }
  [transition-direction="forward"] .fade-effect-enter-active {
    box-shadow: 0 0 10px rgba(0,0,0,.15);
  }
  [transition-direction="forward"] .fade-effect-enter-to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    // opacity: 1;
    // z-index: 2;
    z-index: 9999;
  }
  [transition-direction="forward"] .fade-effect-leave {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    // opacity: 0.8;
    // z-index: 1;
  }
  [transition-direction="forward"] .fade-effect-leave-to {
    -webkit-transform: translate3d(-33%, 0, 0);
    transform: translate3d(-33%, 0, 0);
    // opacity: 0;
    // z-index: 1;
  }

  [transition-direction="back"] .fade-effect-enter {
    -webkit-transform: translate3d(-33%, 0, 0);
    transform: translate3d(-33%, 0, 0);
    // opacity: 0.8;
    // z-index: 1;
    z-index: 9999;
  }
  [transition-direction="back"] .fade-effect-enter-to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    // opacity: 1;
    // z-index: 1;
    z-index: 9999;
  }
  [transition-direction="back"] .fade-effect-leave {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    // opacity: 1;
    // z-index: 2;
  }
  [transition-direction="back"] .fade-effect-leave-active {
    box-shadow: 0 0 10px rgba(0,0,0,.15);
  }
  [transition-direction="back"] .fade-effect-leave-to {
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
    // opacity: 1;
    // z-index: 2;
  }
</style>



