const user = {
  state: {
    isLogin: '',
    userInfo: '', // 用户信息
    chargerId: '' // 网点id、机柜id
  },
  mutations: {
    SET_LOGIN: (state, bol) => {
      state.isLogin = bol
    },
    USERINFO: (state, obj) => {
      state.userInfo = obj
    },
    SET_CHAEGERID: (state, obj) => {
      state.chargerId = obj
    }
  },
  actions: {
    setLogin ({commit}, bol) {
      commit('SET_LOGIN', bol)
    },

    setUserInfo ({commit}, obj) {
      console.log(obj)
      commit('USERINFO', obj)
    },

    setChargerId ({commit}, obj) {
      commit('SET_CHAEGERID', obj)
    }

    // // 登录
    // LoginByUser ({commit}, userInfo) {
    //   return new Promise((resolve, reject) => {})
    // }
  }
}

export default user



