<template>
  <div>
    <transition name="delay">
      <div class="mask" v-if="toastTip"></div>
    </transition>
    <transition name="fade">
      <div class="toast" v-if="toastTip" :class="position==='bottom'?'positionBottom':''">
        <div class="icon" v-if="icon">
          <!-- <img v-if="icon==='addSuccess'" src="../../image/global/right.png"> -->
        </div>
        <div class="describe">{{describe}}</div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  props: {
    toastTip: {
      type: Boolean,
      default: false
    },
    describe: {
      type: String,
      default: ''
    },
    icon: {
      type: String,
      default: ''
    },
    position: {
      type: String,
      default: ''
    }
  },
  watch: {
    toastTip (newValue, oldValue) {
      if (newValue) {
        let timer = setTimeout(() => {
          this.$emit('update:toastTip', false)
          clearTimeout(timer)
        }, 2000)
      }
    }
  }
}
</script>

<!-- Add  "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@keyframes waiting{
  0% {
    transform: rotate(0deg)
  }
  100% {
    transform: rotate(180deg)
  }
}
.mask{
  .fs;
}
.toast{
  .wh(auto, auto);
  max-width: 500px;
  background: rgba(0, 0, 0, .7);
  color: #EEEEEE;
  border-radius: 5px;
  .f-center;
  z-index: 998;
  .icon{
    .wh(auto, 22px);
    margin: 10px auto 5px auto;
    position: relative;
    img{
      height: 100%;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-left: -15px;
      margin-top: -11px;
    }
    .waiting{
      margin-left: -10px;
      transform-origin: center;
      animation: waiting .9s ease-out infinite;
    }
  }
  .describe{
    font-size: 28px;
    padding: 16px 30px;
    min-width: 180px;
  }
}
.positionBottom{
  top: 88%;
}

.delay-leave-active{
  transition-delay: .2s;
}
.fade-enter-active,.fade-leave-active{
  transition: all .3s ease-out;
}
.fade-enter, .fade-leave-to{
  opacity: 0;
}
</style>



