import Mock from 'mockjs'

export default {
  validateFun: () => {
    const validate = Mock.mock({
      'result|2-1': true
    })
    return validate
  },

  validateTrue: () => {
    const validate = Mock.mock({
      'result': true
    })
    return validate
  },

  validateFalse: () => {
    const validate = Mock.mock({
      'result': false
    })
    return validate
  },

  scanningFun: () => {
    const type = Mock.mock({
      'type|1': ['lease', 'restitute', 'scanCode']
      // 'type': 'scanCode'
    })
    return type
  }
}



